.badge1 {
    position: relative;
}
.badge1[data-badge]:after {
    content: attr(data-badge);
    position: absolute;
    top: 11px;
    right: 0px;
    font-size: .7em;
    background: #1e88e5;
    color: white;
    width: 18px;
    height: 18px;
    text-align: center;
    line-height: 18px;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 1px #333;
            box-shadow: 0 0 1px #333;
}



